.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(19, 23, 29, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.8s ease 0s;
  pointer-events: none;
  z-index: 101;
  opacity: 0.5;
}

.modal.active {
  opacity: 1;
  background-color: rgba(19, 23, 29, 0.7);
  pointer-events: all;
}

.modal__content {
  background-color: white;
  border-radius: 20px;
  max-width: 400px;
  max-height: 400px;
  padding: 30px;
  transition: all 0.8s ease 0s;
  opacity: 0;
  transform: perspective(500px) translate(0px, -100%) rotate(45deg);
}

.modal.active .modal__content {
  transform: perspective(500px) translate(0px, 0%) rotateX(0deg);
  opacity: 1;
}

.modal-title {
  max-width: 325px;
  max-height: 400px;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  text-align: center;
  color: #000000;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.modal-close {
  border: none;
  background: none;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 20px;
  color: #000000;
  opacity: 1;
  text-decoration: none;
}

.modal-form {
  display: grid;
  align-items: center;
  justify-content: center;
}

.modal-input {
  width: auto;
  height: 25px;
  margin-bottom: 20px;
  border: none;
  border-radius: 6px;
  outline: 1px solid #000000;
}

.modal-input._error {
  outline: 2px solid red;
}

.modal-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-button {
  padding: 10px 15px;
  box-sizing: border-box;
  font-family: NEXT ART;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  border: none;
  background-color: #e2a300;
  text-transform: uppercase;
  text-decoration: none;
  word-wrap: break-word;
  box-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
  border-radius: 6px;
  cursor: pointer;
}

.modal-button:hover {
  background-color: #ecb23e;
}