@font-face {
  font-family: Arodora Pro;
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src: local("Arodora Pro"), url(../fonts/ArodoraPro-Light.otf);
}

@font-face {
  font-family: NEXT ART;
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local("NEXT ART"), url(../fonts/NEXT\ ART_Regular.otf);
}

@font-face {
  font-family: NEXT ART;
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: local("NEXT ART"), url(../fonts/NEXT\ ART_SemiBold.otf);
}

@font-face {
  font-family: NEXT ART;
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: local("NEXT ART"), url(../fonts/NEXT\ ART_Bold.otf);
}

html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #ffffff;
  background-color: #2c2c2c;
}

ul {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.wrapper {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1222px;
}

/* Header styles start */

.page-header {
  min-height: 100vh;
  padding-top: 40px;
  padding-bottom: 50px;
}

.page-header .wrapper {
  position: relative;
}

.header-nav ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header-nav-list li {
  margin-right: auto;
  font-family: "Arodora Pro";
  font-size: 18px;
  line-height: 21px;
}

.header-nav-list li:last-child {
  margin-right: 0;
}

.header-nav-list li a {
  color: #e4e4e4;
}

.header-nav-list li a:hover,
.header-nav-list li a:focus {
  color: #e2a300;
  opacity: 0.5;
}

.header-nav-list li a:active {
  color: #ffc955;
}

.theme-btn {
  margin: 0;
  background-color: #e2a300;
  box-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
  border: none;
  border-radius: 6px;
  cursor: pointer;
  padding: 2px 5px 3px 5px;
  color: #000000;
  font-size: 16px;
  font-weight: 700;
}

.theme-btn:hover {
  background-color: #e29500;
}
/* Темная тема - Dark theme start */
body.dark-theme {
  color: #000000;
  background-color: #e4e4e4;
}

body.dark-theme a {
  color: #000000;
  background-color: #e4e4e4;
}

body.dark-theme .header-info {
  color: #000000;
}

body.dark-theme .header-btn {
  background-color: #e2a300;
}

body.dark-theme .header-image {
  box-shadow: 0px 4px 15px rgba(27, 27, 27, 0.28);
}

body.dark-theme .about {
  background-color: #e4e4e4;
}

body.dark-theme .about-list li {
  background-color: #ffffff;
}

body.dark-theme .about-list li:hover {
  animation-name: dark-change-color;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

@keyframes dark-change-color {
  0% {
    background-color: #ffffff;
  }

  100% {
    transform: rotate(360deg);
    background-color: #2a2a2a;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 1);
  }
}

body.dark-theme .about-title {
  color: #000000;
}

body.dark-theme .about-description {
  background-color: #ffffff;
  color: #000000;
  box-shadow: 0px 4px 15px rgba(27, 27, 27, 0.28);
}

body.dark-theme .about-photo {
  background-color: #ffffff;
  outline: 6px solid #777777;
  box-shadow: 0px 4px 15px rgba(27, 27, 27, 0.28);
}

body.dark-theme .svg3-circle {
  stroke: #ffffff;
}

body.dark-theme .portfolio-title {
  color: #000000;
}

body.dark-theme .slider__swiper--wrapper {
  background-color: #e4e4e4;
}

body.dark-theme .slider-info {
  color: #000000;
}

body.dark-theme .slider-link {
  color: #000000;
  background-color: #e4e4e4;
}

body.dark-theme .slider-link:hover,
body.dark-theme .slider-link:focus {
  color: #ffd364;
  font-size: 19px;
}

body.dark-theme .slider-link:active {
  color: #ffe7ac;
}

body.dark-theme .page-footer {
  background-color: #e4e4e4;
}

body.dark-theme .footer-nav ul li {
  color: #000000;
}

body.dark-theme .footer-nav ul li a {
  color: #000000;
}

body.dark-theme .footer-nav-list li a:hover,
body.dark-theme .footer-nav-list li a:focus,
body.dark-theme .footer-nav-list li a:active {
  color: #e2a300;
  opacity: 0.5;
}

body.dark-theme .footer-contacts li a:hover,
body.dark-theme .footer-contacts li a:focus,
body.dark-theme .footer-contacts li a:active {
  color: #e2a300;
  opacity: 0.5;
}
/* Темная тема - Dark theme end */

.header-info {
  padding-top: 160px;
  max-width: 560px;
  color: #e4e4e4;
  margin: 0;
}

.header-info-title {
  font-family: "NEXT ART";
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
}

.header-info-subtitle {
  padding-top: 27px;
  font-family: NEXT ART;
  font-weight: 700;
  font-size: 72px;
  line-height: 82px;
}

.header-info-description {
  padding-top: 27px;
  padding-bottom: 27px;
  font-family: Arodora Pro;
  font-size: 18px;
  line-height: 21px;
}

.header-btn {
  padding: 14px 38px 13px 41px;
  box-sizing: border-box;
  font-family: NEXT ART;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #000000;
  border: none;
  background-color: #e2a300;
  text-transform: uppercase;
  text-decoration: none;
  word-wrap: break-word;
  box-shadow: 0px 2px 13px rgba(226, 158, 0, 0.48);
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.7s;
}

.header-btn:hover,
.header-btn:focus {
  background-color: #e29500;
  transform: scale(1.2);
  padding: 16px 40px 15px 43px;
  font-size: 19px;
}

.header-btn:active {
  background-color: #6e4b00;
}

.header-info-svg {
  position: absolute;
  top: 50px;
  right: -10%;
}

.header-image {
  width: 350px;
  height: 350px;
  position: absolute;
  top: 310px;
  right: 11.5%;
  border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
}
/* Header styles end */

/* About styles start */
.about {
  min-height: 100vh;
  background-color: #414141;
  padding-bottom: 200px;
}

.about-list {
  padding-top: 113px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
}

.about-list li {
  width: 270px;
  height: 270px;
  background-color: #2a2a2a;
  margin-right: 40px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
  border-radius: 29px;
  transition: all 5.6s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

.about-list li:last-child {
  margin-right: 0px;
}

.about-list li:hover {
  animation-name: change-color;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

@keyframes change-color {
  0% {
    background-color: #2a2a2a;
  }

  100% {
    transform: rotate(360deg);
    background-color: #ffffff;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 1);
  }
}

.about-list li p {
  padding: 70px 61px 135px 61px;
  font-family: NEXT ART;
  font-weight: 700;
  font-size: 48px;
  line-height: 54px;
  text-align: center;
  color: #00c4f0;
}

.about-info {
  position: relative;
  padding-top: 93px;
}

.about-title {
  position: relative;
  font-family: NEXT ART;
  font-weight: 400;
  font-size: 48px;
  line-height: 54px;
  text-align: center;
  color: #e4e4e4;
  z-index: 99;
}

.about-description {
  position: relative;
  margin: 0 auto;
  padding: 53px;
  max-width: 496px;
  height: 822px;
  font-family: Arodora Pro;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #ffffff;
  background-color: #2c2c2c;
  border-radius: 14px;
  z-index: 1;
}

.about-photo {
  position: absolute;
  z-index: 2;
  box-sizing: border-box;
  padding: 25px;
  background-color: #2c2c2c;
  background-origin: content-box;
  background-size: 100% 100%;
  outline: 6px solid #686868;
  outline-offset: -25px;
  border-radius: 14px;
}

.photo1 {
  left: 60px;
  top: 95px;
}

.photo2 {
  right: 70px;
  top: 300px;
}

.photo3 {
  left: 90px;
  bottom: -40px;
}

.about-info-svg1 {
  position: absolute;
  top: 50px;
  right: 40px;
  z-index: 0;
}

.about-info-svg2 {
  position: absolute;
  right: 40px;
  bottom: -180px;
  z-index: 0;
}

.about-info-svg3 {
  position: absolute;
  left: -200px;
  top: 90px;
  z-index: 0;
}
/* About styles end */

/* Portfolio styles start */
.portfolio {
  min-height: 963px;
}

.portfolio-title {
  margin: 0;
  padding-top: 101px;
  font-family: NEXT ART;
  font-weight: normal;
  font-size: 48px;
  line-height: 54px;
  color: #ffffff;
  text-align: center;
}

.slider__swiper {
  padding: 50px 0 0 0;
}

.slider__swiper--wrapper {
  margin: 0 auto;
  max-width: 950px;
  background-color: #414141;
  border-radius: 29px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.swiper-slide-active .slider__swiper--wrapper {
  box-shadow: 0px 4px 15px rgba(27, 27, 27, 0.28);
}

.slider__swiper--slide {
  display: flex;
  align-items: center;
}

.slider__swiper--image {
  margin: 50px 50px;
}

.slider__swiper--image img {
  border-radius: 29px;
  max-width: 100%;
}

.swiper-button-next {
  left: 50px;
}

.swiper-button-prev {
  right: 50px;
}
.swiper-button-prev:after,
.swiper-button-next:after {
  color: #e2a300;
  cursor: pointer;
}

.swiper-button-next img,
.swiper-button-prev img {
  background-color: #e2a300;
  border-radius: 50%;
  border: none;
  transition: all ease-in 5s;
  cursor: pointer;
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
  transform: scale(1.3);
}

.slider__swiper--button__next:focus,
.slider__swiper--button__prev:focus {
  transform: scale(0.9);
}

.slider-info {
  color: #ffffff;
  overflow: hidden;
}

.slider-title {
  max-width: 300px;
  margin-top: 60px;
  font-family: "Arodora Pro";
  font-weight: 300;
  font-size: 1.5rem;
  line-height: 1.2;
  text-align: center;
}

.slider-description {
  max-width: 300px;
  padding-top: 25px;
  padding-bottom: 25px;
  text-align: left;
  font-family: "Arodora Pro";
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
}

.slider-link {
  margin-left: 80px;
  text-decoration: none;
  font-family: "Arodora Pro";
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #e2a100;
  transition: all ease-in-out 0.2s;
}

.slider-link:hover,
.slider-link:focus {
  color: #ffd364;
  font-size: 19px;
}

.slider-link:active {
  color: #ffe7ac;
}
/* Portfolio styles end */

/* Footer styles start */
.page-footer {
  min-height: 40vh;
  background-color: #414141;
}

.footer-nav {
  display: flex;
  list-style: none;
  font-family: Arodora Pro;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #e4e4e4;
}

.footer-nav-list {
  padding-top: 50px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  list-style: none;
}

.footer-nav-list li {
  margin-bottom: 18px;
}

.footer-nav-list li:last-child {
  margin-bottom: 0;
}

.footer-nav-list li a {
  text-decoration: none;
  font-family: Arodora Pro;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #e4e4e4;
}

.footer-nav-list li a:hover,
.footer-nav-list li a:focus,
.footer-nav-list li a:active {
  color: #e2a300;
  opacity: 0.5;
}

.footer-contacts {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  list-style: none;
  margin-left: 115px;
}

.footer-contacts li {
  margin-bottom: 18px;
}

.footer-contacts li a {
  text-decoration: none;
  font-family: Arodora Pro;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  color: #e4e4e4;
}

.footer-contacts li a:hover,
.footer-contacts li a:focus,
.footer-contacts li a:active {
  color: #e2a300;
  opacity: 0.5;
}

.footer-contacts li p {
  margin: 0;
}
/* Footer styles end */

