@media (max-width: 510px) {
  .about-description {
    max-width: auto;
    height: auto;
  }
  .slider__swiper--slide {
    justify-content: center;
  }
  .slider__swiper--button__next,
  .slider__swiper--button__prev {
    top: 50%;
  }
  .slider__swiper--button__next {
    left: 5%;
  }
  .slider__swiper--button__prev {
    left: 85%;
  }
}

@media (max-width: 490px) {
  .header-nav ul {
    flex-direction: column;
  }
  .header-nav-list li {
    margin-right: 0px;
    margin-bottom: 20px;
  }
  .header-info {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
  }
  .about-list {
    padding-top: 70px;
  }
  .about-description {
    font-size: 18px;
    line-height: 22px;
  }
  .portfolio-title {
    font-size: 42px;
    line-height: 50px;
  }
  .slider-info {
    max-width: 250px;
    margin: 0 auto;
  }
  .slider-title {
    max-width: 250px;
    margin-top: 20px;
  }
  .slider-description {
    text-align: center;
    font-size: 16px;
    width: 250px;
    padding-top: 30px;
    padding-bottom: 5px;
  }
  .page-footer {
    padding-left: 10px;
  }
  .footer-nav-list li a {
    font-size: 16px;
  }
  .footer-contacts {
    margin-left: 10px;
  }
}

@media (max-width: 320px) {
  body {
    max-width: 320px;
  }
  .header-info {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
  }
  .header-info-subtitle {
    font-size: 38px;
    line-height: 42px;
  }
  .about-description {
    max-width: 100%;
    margin: 0 auto;
    padding: 25px;
    font-size: 18px;
    line-height: 22px;
  }
  .portfolio-title {
    font-size: 42px;
    line-height: 50px;
  }
  .slider-info {
    max-width: 210px;
    margin: 0 auto;
  }
  .slider-title {
    max-width: 260px;
  }
  .slider-description {
    font-size: 16px;
    width: 210px;
    padding-top: 15px;
    padding-bottom: 5px;
  }
  .slider-link {
    margin-left: 45px;
  }
  .page-footer {
    padding-left: 10px;
  }
  .footer-contacts {
    margin-left: 10px;
  }
}
